import React from "react";
import { Typography, Button, Box, AppBar, Toolbar } from "@mui/material";
//import styled from "styled-components";
import { styled as styledMaterial } from '@mui/material/styles';
import styled from "@emotion/styled"
import { css } from "@emotion/react"
import { colors } from "../GlobalCSSVariables"
import { withPrefix } from 'gatsby';
import HighlightSection from './HighlightSection'

const StyledParentSection = styled.div`
    background-color: ${colors.darkBrown};
    color: ${colors.white};
    text-align: center;
`
const ParentSection = ({ title, children, className, showMoreLink }) => {
    return (
        <StyledParentSection>
            <Typography variant="h1" component="h1" gutterBottom>
                {title}
            </Typography>
            {children}
            <div>
                {showMoreLink && <Typography variant="h2" component="h2">More</Typography>}
                &nbsp;
            </div>
        </StyledParentSection>
    );
};

export default ParentSection