import React from "react";
import { Typography, Button, Box, AppBar, Toolbar } from "@mui/material";
//import styled from "styled-components";
import { styled as styledMaterial } from '@mui/material/styles';
import styled from "@emotion/styled"
import { css } from "@emotion/react"
import { colors } from "../GlobalCSSVariables"
import { withPrefix } from 'gatsby';

const TextSection = styled.div`
    background-color: ${colors.mediumBrown};
    text-align: center;
    width:calc(40vw - ((100vw - 100%)/2));
    height: 47vh;
    float:left;
    margin-bottom:50px;
`;
const PhotoSection = styled.div`
    background-color: ${colors.mediumBrown};
    background-position: top center;
    background-size: cover;
    height: 47vh;
    width: calc(60vw - ((100vw - 100%)/2));
    float:left;
    margin-bottom:50px;
`
const StyledHightlightSection = styled.div`
    background-color: ${colors.darkBrown};
    color: ${colors.white};
    padding: 10px;
    font-weight: 400;
    text-align: right;
    a {
        color: ${colors.white};
    }
`
const Footer = () => {
    return (
        <StyledHightlightSection>
            Designed by: <a href="mailto:robert@definedinnovations.com">Defined Innovatinos, LLC</a>
        </StyledHightlightSection>
    );
};

export default Footer