import React from "react";
import { Typography, Stack } from "@mui/material";
//import styled from "styled-components";
import styled from "@emotion/styled"
import { css } from "@emotion/react"
import { colors, MediaQueries } from "../GlobalCSSVariables"
import { withPrefix } from 'gatsby';

const TextSection = styled.div`
    background-color: ${colors.mediumBrown};
    text-align: center;
    width: 100%;
    padding-bottom: 25px;
    ${MediaQueries.md}  {
        width:calc(40vw - ((100vw - 100%)/2));
        height: 47vh
    }
    ;
`;
const PhotoSection = styled.div`
    background-color: ${colors.mediumBrown};
    background-position: top center;
    background-size: cover;
    height: 47vh;
    width: 100%;
    ${MediaQueries.md}  {
        width: calc(60vw - ((100vw - 100%)/2));
    }
`
const StyledStack = styled(Stack)`
    margin-bottom:50px;
`
const HighlightSection = ({ title, children, className, photoIsLeft, photoSrc }) => {
    const stackDirection = photoIsLeft ? "row" : "row-reverse";
    return (
        <StyledStack
            direction={{ xs: 'column', md: stackDirection }}
            justifyContent="center"
            alignItems="flex-start"
            spacing={0}
        >
            <PhotoSection css={css`background-image: url(${withPrefix(photoSrc)})`} />
            <TextSection>
                <Typography variant="h2" component="h2" gutterBottom>
                    {title}
                </Typography>
                <Typography variant="paragraph" component="p" comp align="center">
                    {children}
                </Typography>
            </TextSection>
        </StyledStack>
    );
};

export default HighlightSection