import React from "react";
import { Typography, Button, Box, AppBar, Toolbar } from "@mui/material";
//import styled from "styled-components";
import { styled as styledMaterial } from '@mui/material/styles';
import styled from "@emotion/styled"
import { css } from "@emotion/react"
import { colors } from "../GlobalCSSVariables"
import { withPrefix } from 'gatsby';

const StyledTextSection = styled.div`
  background-color: ${colors.gray};
  text-align: center;
    padding-bottom: 50px;
`;

const TextSection = ({ title, children, className, htmlId }) => {
    return (
        <StyledTextSection className={className} htmlId={htmlId}>
            <Typography variant="h1" component="h1" gutterBottom>
                {title}
            </Typography>
            <Typography variant="paragraph" component="p"  align="center">
                {children}
            </Typography>
        </StyledTextSection>
    );
};

export default TextSection